import React from 'react'
import PropTypes from 'prop-types'
import './ElementOfHead.css'

class ElementOfHead extends React.Component {
  constructor(props){
    super(props);
    this.name = props.name;
  }

  render () {
    return(
      <div className="ElementOfHead">
        <text class="headText">{this.name}</text>
      </div>
    )
  }
}

export default ElementOfHead;
