import React from 'react'
import PropTypes from 'prop-types'
import "./Education.css"
import Career from "./Career.js"

class Education extends Career {
  constructor(props){
    super(props);
    this.organisation = props.place;
    this.start = props.start;
    this.end = props.end;
  }

  getTable = ()=>{
    return(
      <table className="info">
        <colgroup>
          <col className='col1'></col>
          <col className='col2'></col>
        </colgroup>
        <tbody>
          <tr>
            <td>Поступил: </td>
            <td>{this.start.toLocaleDateString()}</td>
          </tr>
          <tr>
            <td>Должность: </td>
            <td>{this.position}</td>
          </tr>
          <tr>
            <td>Обязанности: </td>
            <td><p className="text">{this.responsibility}</p></td>
          </tr>
          <tr>
            <td>Уволился: </td>
            <td>{this.end.toLocaleDateString()}</td>
          </tr>
          <tr>
            <td>Прокачал скилы: </td>
            <td><p className="text">{this.skill}</p></td>
          </tr>
          <tr>
            <td>Что понял </td>
            <td><p className="text">{this.mark}</p></td>
          </tr>
        </tbody>
      </table>
    )
  }

}

export default Education;
